
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html, body {
  min-height: 100%;
  background: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Avenir', Helvetica, Arial, sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
  margin-left: 0;
  padding-left: 24px;
  margin-top: 2rem;
}
li {
  margin-top: 6px;
  line-height: 1.45;
}
.container {
  max-width: 900px;
  margin: 0 auto;
  text-align: left;
}
.container p {
  line-height: 1.45;
  margin-top: 2rem;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-bottom: 48px;
}
.Header {
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-height: 200px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Header__logo {
  max-width: 236px;
  width: 100%;
}

.Alert {
  padding: 1rem;
  background: #169a3e;
  color: #fff;
  font-weight: 500;
  font-weight: 300;
}
.Alert .container {
  text-align: justify;
}
.Alert__title {
  font-weight: 500;
  line-height: 1.6;
}
.Alert strong {
  font-weight: 500;
}
.CompanieList {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding-top: 24px;
}
.CompanieList__item {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  will-change: transform;
  text-decoration: none;
  margin: 6px;
  cursor: pointer;
}
.CompanieList__item:hover {
  /*transform: scale(1.1);*/
  -webkit-box-shadow: 0 12px 48px rgba(0, 0, 0, 0.1);
          box-shadow: 0 12px 48px rgba(0, 0, 0, 0.1);
  z-index: 2;
}
.CompanieList__imageBox {
  width: 300px;
  height: 300px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.CompanieList__info {
  background: #ffffff;
  padding: 1rem;
  color: #333;
  border-top: 1px solid #f1f1f1;
  max-width: 300px;
}
.CompanieList__name,
.CompanieList__description {
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.CompanieList__name {
  font-weight: 600;
}
.CompanieList__description {
  font-weight: 300;
  margin-top: 6px;
  color: #666;
}
.CompanieList__image {
  max-width: 200px;
  max-height: 140px;
}

/*Modal*/
.Companie {
  text-align: center;
}
.Companie__image img {
  max-height: 180px;
}
.Companie__image {
  max-width: 240px;
  width: 100%;
  display: block;
  margin: 0 auto;
}
.Companie__name {
  font-weight: 800;
  font-size: 1.8rem;
  margin-top: 2rem;
  color: #333;
  display: inline-block;
}
.Companie__description {
  margin-top: 2rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.45;
}
.Link__icon {
  display: inline;
  width: 24px;
  height: 24px;
  fill: rgba(0, 0, 0, 0.6);
  margin-top: -10px;
  margin-left: 6px;
}
.Alert.is-open > .Alert__icon {
  -webkit-transform: rotate(180deg)translateY(10px);
          transform: rotate(180deg)translateY(10px);
}
.Alert__icon {
  width: 48px;
  height: 48px;
  cursor: pointer;
  fill: rgba(255, 255, 255, 1);
}
@media screen and (min-width: 768px) {
.Companie__name {
    font-size: 2.4rem;
}
.Companie__description {
    font-size: 1.4rem;
}
}

.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  -webkit-animation: fadeIn 0.15s ease-in-out forwards;
          animation: fadeIn 0.15s ease-in-out forwards;
  background: rgba(255, 255, 255, 0.95);
}
.Modal__dialog {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  padding: 1rem;
}
.Modal.is-closing {
  -webkit-animation: fadeOut 0.15s ease-in-out forwards;
          animation: fadeOut 0.15s ease-in-out forwards;
}
.Modal__close {
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  position: fixed;
  background: rgba(255, 255, 255, 0.9);
}
.Modal__closeButton {
  position: fixed;
  right: 0;
  top: 0;
  height: 4rem;
  width: 4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
}
@-webkit-keyframes fadeIn{
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn{
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeOut{
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes fadeOut{
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
